import "./App.css";
import AppBody from "./Content/AppBody.js";
import { MuiThemeProvider, createTheme, makeStyles } from "@material-ui/core/styles";
import { useEffect, useState } from "react";
import { Container } from "@material-ui/core";
import logo from "./Content/imgs/loading.svg";
import CircularProgress from '@material-ui/core/CircularProgress';

// import { Typography } from "@material-ui/core";
const theme = createTheme({
  palette: {
    primary: {
      main: "#251056",
      contrastText: "white",
    },
    secondary: {
      main: "#dc004e",
      contrastText: "#343f56",
    },
  },
});
const useStyles = makeStyles((theme) => ({
  logo:{
      width: "80%",
      [theme.breakpoints.up("sm")]:{
        width: "30%",
      }
  }
}))

function App() {
  const [opacity, setOpacity] = useState(0);
  const [display, setDisplay] = useState("block");
  const classes = useStyles();

  useEffect(() => {
    const loading = document.getElementById("loading");
    setTimeout(() => {
      setDisplay("none");
      setOpacity(1);
    }, 5000);
    setTimeout(() => {
      loading.parentNode.removeChild(loading);
    }, 10000);
  }, []);
  return (
    <>
      <div style={{ display: display }} id="loading">
        <Container
          style={{
            display: "flex",
            justifyContent: "center",
            flexDirection:"column",
            minHeight: "100vh",
            minWidth: "100vw",
            alignItems: "center",
            overflow: "hidden",
            position: "fixed",
          }}
        >
          <img src={logo} alt="loading" className={classes.logo} />
        </Container>
      </div>
      <div className="bg" style={{ opacity: opacity }}>
        <MuiThemeProvider theme={theme}>
          <AppBody />
        </MuiThemeProvider>
      </div>
    </>
  );
}

export default App;
