import { React, useState, useEffect } from "react";
import "../../App.css";
import Signup from "../forms/Signup";
import Dgeneral from "../forms/Dgeneral";
import Ddescription from "../forms/Ddescription";
import Findustry from "../forms/Findustry";
import Preview from "../forms/Preview";
import { useStep } from "react-hooks-helper";
import { useHistory } from "react-router";

const steps = [
  { id: "Preview", index: 0 },
  { id: "Submite", index: 1 },
];

function Switcher(props) {
  const { Serrors, setErrors } = props;
  const navig = props.navigation;
  useEffect(() => {
    if (Object.keys(Serrors).length > 0) {
      if (checkiferr(Serrors) === true) console.log("errr");
      else {
        navig.next();
        setErrors({});
      }
    }
    window.scrollTo(0, 0);
  }, [Serrors, setErrors]);

  const checkiferr = (arr) => {
    let i = false;
    Object.values(arr).forEach((el) => {
      if (el !== "") i = true;
    });
    return i;
  };

  const handlSNext = () => {
    props.checkSignup(props, props.Phone,  setErrors);
  };
  const handlDgNext = () => {
    props.checkDgeneral(props, setErrors);
  };
  const handlDdNext = () => {
    props.checkDdescription(props, setErrors);
  };
  const handlfiNext = () => {
    props.checkFindustry(props, setErrors);
  };
  const handlback = () => {
    props.setErrors({});
    props.navigation.previous();
  };

  const { step, navigation } = useStep({
    steps,
    initialStep: 0,
  });
  const [loading, setLoading] = useState(true);
  const values = [
    "FirstName",
    "LastName",
    "username",
    "password",
    "Rpassword",
    "email",
    "Sname",
    "epitch",
    "sds",
    "adresse",
    "num",
    "dq15",
    "dq1",
    "dq2",
    "dq3",
    "dq4",
    "dq5",
    "dq6",
    "dq7",
    "dq8",
    "dq9",
    "dq10",
    "dq11",
    "dq12",
    "dq13",
    "dq14",
  ];
  const clearValues = (setValue, setPhone, setPic) => {
    values.map((elem) => setValue({ target: { name: elem, value: "" } }));
    setPhone("");
    setPic("");
  };
  const history = useHistory();
  const addStartup = async () => {
    navigation.next();
    const account = {
      firstname: props.FirstName,
      lastname: props.LastName,
      password: props.password,
      email: props.email,
      phone: props.Phone,
    };
    const Ntask = {
      // name: props.Sname,
      //FirstName: props.FirstName,
      //LastName: props.LastName,
      //password: props.password,
      // Rpassword: props.Rpassword,
      //email: props.email,
      Sname: props.Sname,
      epitch: props.epitch,
      website: props.website,
      sds: props.sds,
      // adress: props.adresse,
      form: {
        dq15: props.dq15,
        dq1: props.dq1,
        dq2: props.dq2,
        dq3: props.dq3,
        dq4: props.dq4,
        dq5: props.dq5,
        dq8: props.dq8,
        dq6: props.dq6,
        dq7: props.dq7,
        dq9: props.dq9,
        dq10: props.dq10,
        dq11: props.dq11,
        dq12: props.dq12,
        dq13: props.dq13,
        dq14: props.dq14,
      },
      logo: props.pictures,
    };
    const accountres = await fetch(
      "http://172.16.177.114:5000/api/v1/auth/register",
      {
        method: "POST",
        headers: {
          "Content-type": "application/json",
        },
        body: JSON.stringify(account),
      }
    );
    const token = await accountres.json();
    console.log(token.token);
    if (token.token) {
      const res = await fetch("http://172.16.177.114:5000/api/v1/startups", {
        method: "POST",
        headers: {
          "Content-type": "application/json",
          Authorization: `Bearer ${token.token}`,
        },
        body: JSON.stringify(Ntask),
      });
      const data = await res.json();
      props.setStartup([...props.startup, data]);
    }
    clearValues(props.setValue, props.setPhone, props.setPic);
    setTimeout(() => {
      props.navigation.go(0);
      history.push("/");
    }, 3000);
    setLoading(false);
  };
  switch (props.step.indx) {
    case 0:
      return (
        <Signup
          ftype="SignUp"
          Btype="Single"
          navigation={props.navigation}
          handlNext={handlSNext}
          {...props}
        />
      );
    case 1:
      return (
        <Dgeneral
          Btype="Double"
          navigation={props.navigation}
          handlNext={handlDgNext}
          handlback={handlback}
          {...props}
        />
      );
    case 2:
      return (
        <Ddescription
          Btype="Double"
          navigation={props.navigation}
          handlNext={handlDdNext}
          handlback={handlback}
          {...props}
        />
      );
    case 3:
      return (
        <Findustry
          Btype="Double"
          navigation={props.navigation}
          handlNext={handlfiNext}
          handlback={handlback}
          {...props}
        />
      );
    case 4:
      return (
        <Preview
          Btype="Double"
          end="end"
          navigation={props.navigation}
          handlback={handlback}
          addStartup={addStartup}
          loading={loading}
          {...props}
          step={step}
        />
      );
    default:
  }
}
export default Switcher;
