const fetchips = async (where) => {
  const res = await fetch(`http://localhost:7000/${where}`);
  const data = await res.json();
  return data;
};





export const emailchecker = async (email) => {
  const item  = {
    email : email
  }
  const checkemail = await fetch(
    "http://172.16.177.114:5000/api/v1/auth/check",
    {
      method: "POST",
    headers: {
      "Content-type": "application/json",
    },
    body: JSON.stringify(item),
  }
  );
  const res = await checkemail.json();
  return(res.data);
}



export const Snamechecker = async (Sname) => {
  const item  = {
    Sname : Sname
  }
  const checkemail = await fetch(
    "http://172.16.177.114:5000/api/v1/startups/check",
    {
      method: "POST",
    headers: {
      "Content-type": "application/json",
    },
    body: JSON.stringify(item),
  }
  );
  const res = await checkemail.json();
  return(res.data);
}










