import { React, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import { useStep } from "react-hooks-helper";
import Inscription from "./component/inscription.js";
import { useForm } from "react-hooks-helper";
import Home from "./component/Home/Home";
import {Contactus} from "./component/Home/Contactus/Contactus";
import Signin from "./forms/Signin";
import { Switch, Route } from "react-router-dom";
import NotFound from "./component/NotFound.js";
import Dashboard from "./component/dashboard/Dashboard";
import { Container } from "@material-ui/core";
import { isPossiblePhoneNumber } from "react-phone-number-input";
import  {checkSignup,checkDgeneral,checkDdescription,checkFindustry, Phone } from "./fetchingData/errortootls"
const steps = [
  { id: "About your Account", indx: 0 },
  { id: "Startup Description", indx: 1 },
  { id: "Market/Intellectual property ", indx: 2 },
  { id: "Financials/Co-founding team", indx: 3 },
  { id: "Preview", indx: 4 },
];

function AppHead(props) {
  // const addStartup = async () => {
  //   const Ntask = {
  //     name: Sname,
  //     desc: dq15
  //   }
  //   const res = await fetch("http://localhost:7000/startup", {
  //     method: "POST",
  //     headers: {
  //       "Content-type": "application/json",
  //     },
  //     body: JSON.stringify(Ntask),
  //   });
  //   const data = await res.json();
  //   props.setStartup([...props.startup, data]);
  //   navigation.next();
  // };
  const useStyles = makeStyles((theme) => ({
    all: {
      [theme.breakpoints.up("md")]: {
        // paddingRight: theme.spacing(20),
        // paddingLeft: theme.spacing(20),
      },
    },
  }));

  const defaultValues = {
    FirstName: "",
    LastName: "",
    username: "",
    password: "",
    Rpassword: "",
    email: "",
    Sname: "",
    website: "",
    epitch: "",
    sds: "Is",
    dq1: "",
    dq2: "",
    dq3: "",
    dq4: "",
    dq5: "",
    dq6: "",
    dq7: "",
    dq8: "",
    dq9: "",
    dq10: "",
    dq11: "",
    dq12: "",
    dq13: "",
    dq14: "",
    dq15: "",
  };

  const [
    {
      FirstName,
      LastName,
      username,
      password,
      Rpassword,
      email,
      Sname,
      website,
      epitch,
      sds,
      dq1,
      dq2,
      dq3,
      dq4,
      dq5,
      dq6,
      dq7,
      dq8,
      dq9,
      dq10,
      dq11,
      dq12,
      dq13,
      dq14,
      dq15,
    },
    setValue,
  ] = useForm(defaultValues);

  const { step, navigation } = useStep({
    steps,
    initialStep: 0,
  });
  const [pictures, setPic] = useState("");
  const [Phone, setPhone] = useState("");
  const onDrop = (picture, file) => {
    setPic(file);
  };
  const [Serrors, setErrors] = useState({});
  const classes = useStyles();

  return (
    <Switch>
      <Route exact path="/">
        <Home />
      </Route>
      <Route exact path="/Apply">
        <Container maxWidth="xl">
          <Inscription
            checkSignup={checkSignup}
            checkDgeneral={checkDgeneral}
            checkDdescription={checkDdescription}
            checkFindustry={checkFindustry}
            Serrors={Serrors}
            setErrors={setErrors}
            setPic={onDrop}
            step={step}
            steps={steps}
            navigation={navigation}
            username={username}
            FirstName={FirstName}
            LastName={LastName}
            email={email}
            Phone={Phone}
            setPhone={setPhone}
            password={password}
            Rpassword={Rpassword}
            Sname={Sname}
            epitch={epitch}
            website={website}
            sds={sds}
            dq1={dq1}
            dq2={dq2}
            dq3={dq3}
            dq4={dq4}
            dq5={dq5}
            dq8={dq8}
            dq6={dq6}
            dq7={dq7}
            dq9={dq9}
            dq10={dq10}
            dq11={dq11}
            dq12={dq12}
            dq13={dq13}
            dq14={dq14}
            dq15={dq15}
            pictures={pictures}
            setValue={setValue}
            setStartup={props.setStartup}
            {...props}
          />
        </Container>
      </Route>
      <Route exact path="/signin">
        <Signin ftype="LOGIN" className={classes.login} Serrors={{}} />
      </Route>
      <Route path="/Account">
        <Dashboard
          startup={props.startup}
          setStartup={props.setStartup}
          startups={props.startups}
          setStartups={props.setStartups}
        />
      </Route>
      <Route path="/Contact">
        <Contactus />
      </Route>
      <Route component={NotFound} />
    </Switch>
  );
}
export default AppHead;
